




















































































































































































































































































































































.overview {
    margin-top: 0.24rem;

    .search {
        padding: 16px 0 0px 0;

        .li {
            &:last-child {
                margin-bottom: 0;
            }

            display: flex;
            margin-bottom: 16px;

            .item {
                width: 25%;
                display: flex;
                line-height: 32px;
                height: 32;

                .label {
                    min-width: 70px;
                    margin-right: 2px;
                }

                .rt {
                    flex: 1;
                }

                .btns {
                    width: 100%;
                    display: flex;
                    justify-content: flex-end;
                }
            }
        }
    }

    /deep/ .item .el-input__inner {
        width: 270px;
        height: 32px;
        line-height: 32px;
    }
}
