

















































































































































































































































































































































































































































































.upload-box {
    width: 100%;
    display: flex;
    flex-flow: row wrap;

    align-content: flex-start;

    .logo-img {
        width: 75px;
        height: 75px;
        line-height: 0;
        padding: 20px;
        box-sizing: border-box;
    }

    .noBottom {
        padding-bottom: 0 !important;
    }

    .item {
        &.allLine {
            flex: 0 0 100%;
        }

        .box {
            width: 75px;
            height: 75px;
            margin: 10px;
            box-sizing: border-box;
            background-color: #F4F4F4;
        }

        flex: 0 0 50%;
        padding-bottom: 24px;

        .title {
            line-height: 20px;
            color: #000;
            font-weight: 600;
            margin-bottom: 24px;
        }

        .notice {
            color: #999999;
            font-size: 12px;
        }

        .files {
            display: flex;

            flex: 1;

            .label {
                width: 70px;
            }
        }
    }

    .prev-box {
        height: 104px;
        line-height: 30px;
        font-size: 12px;
        padding: 10px;
        color: #2E4AF7;
    }

    /deep/ .hide .el-upload--picture-card {
        display: none;
    }
}
