






























































.over-box {
    background: rgba(46, 74, 247, 0.03);
    border-radius: 2px;
    border: 1px solid rgba(46, 74, 247, 0.31);
    padding: 10px 20px;
    color: #333;

    .title {
        font-family: 'PFC'
    }
}

.items {
    display: flex;
    align-items: center;

    justify-content: space-between;

    .item-li {
        line-height: 24px;
    }

    .count {
        cursor: pointer;
        text-decoration: underline;
        font-size: 14px;
        color: #000;
        font-family: "PFC"
    }
}
