    display: flex;
    flex-flow: row wrap;

    .item {
        width: 50%;
        margin-bottom: 22px;
    }

    /deep/ .el-form-item__error {
        width: 100%;
    }

    &.onlyShow {
        /deep/ .el-textarea__inner {
            background-color: transparent;
            border: 0;
        }
    }

    .item {
        &.disable {

            /deep/ .el-input.is-disabled .el-input__inner,
            /deep/ .el-range-editor.is-disabled {
                background-color: rgba(46, 74, 247, 0.03);
                color: #999;
            }

            /deep/ .el-range-editor.is-active {
                border-color: #E4E7ED;
            }


        }

        display: flex;
        line-height: 32px;
        height: 32;

        .label {
            min-width: 119px;
            margin-right: 2px;
            color: #333;
            text-align: right;
        }

        .rt {
            flex: 1;
        }

        .btns {
            width: 100%;
            display: flex;
            justify-content: flex-end;

        }
    }

    .important {
        color: #FF2B73;
    }

    /deep/ .item .el-input__inner,
    /deep/ .el-textarea__inner {
        width: 270px;
        height: 32px;
        line-height: 32px;
    }

    /deep/ .el-form-item {
        margin-bottom: 0;
    }

    /deep/ .el-form-item__content {
        display: flex;
        line-height: 32px;
    }

    /deep/ .el-radio-group {
        padding-top: 8px;
    }
}
