































































































































































































































































.form {
    padding-left: 15px;

    .li {

        display: flex;
        margin-bottom: 22px;

        .item {
            width: 100%;
            display: flex;
            line-height: 32px;
            height: 32;

            .label {
                min-width: 92px;
                margin-right: 2px;
                color: #333;
                text-align: right;
            }

            .rt {
                flex: 1;
            }

            .btns {
                width: 100%;
                display: flex;
                justify-content: flex-end;

            }

            .textarea {
                width: 270px;
            }
        }
    }

    .important {
        color: #FF2B73;
    }

    /deep/ .item .el-input__inner {
        width: 270px;
        height: 32px;
        line-height: 32px;
    }

    /deep/ .el-form-item {
        margin-bottom: 0;
    }

    /deep/ .el-form-item__content {
        display: flex;
        line-height: 32px;
    }
}
