



















































































































































































.files {
    display: flex;
    flex-wrap: wrap;

    .logo-img {
        width: 75px;
        height: 75px;
        padding: 20px;
        box-sizing: border-box;
    }

    .item {

        .box {
            width: 75px;
            height: 75px;
            margin: 10px;
            box-sizing: border-box;
            background-color: #F4F4F4;
        }

        display: flex;
        justify-content: center;
        flex-direction: column;
        margin-right: 10px;
        margin-bottom: 16px;

        img {
            width: calc(100% - 20px);
            height: calc(100% - 20px);
            margin: 10px;
            box-sizing: border-box;
        }

        .img {
            width: 95px;
            height: 95px;
            border-radius: 2px;
            background-color: #fff;
            border: 1px solid #c0ccda;
            border-radius: 2px;
            box-sizing: border-box;

            margin: 0;
            display: inline-block;
            position: relative;

            .btns {
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                display: flex;
                justify-content: center;
                align-items: center;
                transition: all 0.3s;

                &:hover {
                    background-color: rgba(0, 0, 0, 0.5);

                    i {
                        display: block;
                    }
                }


                i {
                    display: none;
                    font-size: 20px;
                    color: #fff;
                    cursor: pointer;
                    margin-left: 10px;
                    margin-right: 10px;
                }
            }
        }

        .des {
            border-radius: 2px;
            border: 1px solid #DCDCDC;
            text-align: center;
            padding: 3px 8px;
            margin-top: 10px;
            font-size: 12px;
            text-align: center;
            box-sizing: border-box;
            width: 95px;

            /deep/ span {
                display: block;
                width: 80px;
                text-overflow: ellipsis;
                overflow: hidden;
                white-space: nowrap;
            }

            /deep/ .el-button {
                padding: 0;
                font-size: 12px;
                height: 20px;
                border: 0;
            }

            /deep/.el-button:focus,
            /deep/.el-button:hover {
                color: #2E4AF7;
                border-color: transparent;
                background-color: transparent;
            }
        }
    }
}
